<template>
  <div class="course-practise-view">
    <head-top title="随堂练习评卷">
    </head-top>

    <div class="sub_nav">
      {{libraryInfo.name || '查看作答'}}
    </div>
    <div class="sub_module flex-between">
      <span>题数:{{libraryInfo.number}}</span>
      <span>总分:{{libraryInfo.score}}</span>
    </div>

    <div class="sub_nav">
      <span>题目</span>
      <span>
        <van-button type="primary" size="small" v-if="selectedIndex>1" @click="next(-1)" style="margin-right: 10px;">
          上一题
        </van-button>
        <van-button type="primary" size="small" v-if="selectedIndex< libraryInfo.number" @click="next(1)">
          下一题
        </van-button>
      </span>
    </div>
    <template v-if="practiseInfo">
      <div class="sub_module practise-item">
        <p class="practise-title">
          <span>{{ selectedIndex }}</span>
          <span>{{ '、【'+ practiseTypeOptions[practiseInfo.type-1].label +'】'+ practiseInfo.title}} </span>
          <span>（{{practiseInfo.score}}分）</span>
        </p>

        <div v-if="[1, 2].indexOf(practiseInfo.type) > -1">
          <h4>选项：</h4>
          <p class="practise-options" v-for="(sub, k) in practiseInfo.options" :key="k">{{sub.name +'、'+sub.value}}</p>
        </div>

        <h4>答案：</h4>
        <div class="practise-content">{{practiseInfo.answer||'无'}}</div>
        <h4>解析：</h4>
        <div class="practise-content">{{practiseInfo.analysis||'无'}}</div>
      </div>

      <template v-if="practiseInfo.type !== 4">
        <div class="sub_nav">
          <span>答题统计</span>
          <span>
        <span>正确率：{{practiseInfo.trueRate}}</span>
        <span style="padding-left: 10px;">错误率：{{practiseInfo.falseRate}}</span>
      </span>
        </div>
        <div class="sub_module">
          <div class="flex-wrap-between" v-if="practiseInfo.answers && practiseInfo.answers.length>0">
            <div v-for="(item, index) in practiseInfo.answers" :key="index" class="van-hairline--surround list-col">
              <span>{{item.name}}：{{item.total || 0}}</span>
            </div>
            <div v-for="k in (4 - practiseInfo.answers.length % 4) % 4" :key="-k" class=" list-col">
            </div>
          </div>
          <r-lack-data v-else></r-lack-data>
        </div>
      </template>

      <div class="sub_nav">
        <span>提交成员</span>
        <span>{{submitInfo.subNum}} / {{submitInfo.total}}</span>
      </div>
      <div class="sub_module">
        <span v-if="submitInfo.persons.length===0">暂无数据</span>
        <div class="flex-wrap-between" v-else>
          <div v-for="(sub, k) in submitInfo.persons" :key="k" class="student_card van-hairline--surround"
               @click="toAnswer(sub)">
            <img :src="sub.imgSrc" class="col_4" alt=""/>
            <div>{{sub.studentName}}</div>
          </div>
          <div class="student_card" style="width: 19vw;" v-for="d in Math.abs(4 - submitInfo.persons.length % 4) % 4"
               :key="-d">
          </div>
        </div>
      </div>

      <div class="sub_nav">
        <span>问卷调研</span>
        <van-button v-if=" !practiseInfo.isSurvey" type="info" size="small"
                    @click="toSurvey">
          下发调研
        </van-button>
        <span v-else>已下发</span>
      </div>
      <div class="sub_module"  v-if="practiseInfo.isSurvey">
        <div class="flex-between" v-if="practiseInfo.isSurvey">
          <div class="van-hairline--surround list-col">
            <span>已掌握：{{surveyInfo.yes}}</span>
          </div>
          <div class="van-hairline--surround list-col">
            <span>未掌握：{{surveyInfo.no}}</span>
          </div>
        </div>
        <r-lack-data v-else></r-lack-data>
      </div>
    </template>

    <!-- 人员答题显示-->
    <van-popup v-model="answerPersonsShow" position="bottom" closeable>
      <div class="sub_nav">
        <span v-if="practiseInfo && practiseInfo.type !== 4">作答明细</span>
        <span v-else>答题评分</span>
      </div>
      <div v-if="answerPersonsSelected && practiseInfo">
        <van-field v-model="answerPersonsSelected.studentName" label="提交学生：" disabled></van-field>
        <van-field v-model="practiseInfo.answer" label="正确答案：" disabled></van-field>
        <van-field label="提交答案：" disabled>
          <!-- 此处图片存在的情况需修改-->
          <!--          <div v-html="answerPersonsSelected.answer" slot="input"></div>-->
          <template #input>
            <span v-if="practiseInfo && practiseInfo.type !== 4" :style="{color: '#969799'}">{{answerPersonsSelected.answer}}</span>
            <div v-else class="answer-content" v-html="transform(answerPersonsSelected.answer)"
                 :style="{color: '#969799'}"></div>
          </template>
        </van-field>
        <van-field v-model="practiseInfo.score" label="本题分数：" disabled></van-field>
        <van-field name="stepper" label="作答得分：">
          <template #input>
            <span v-if="practiseInfo && practiseInfo.type !== 4" :style="{color: '#969799'}">{{answerPersonsSelected.score||0}}</span>
            <van-stepper v-else v-model="answerPersonsSelected.score" integer min="0" :max="practiseInfo.score">
            </van-stepper>
            <span style="padding-left: 10px;">分</span>
          </template>
        </van-field>
        <!--        <van-field v-model="answerPersonsSelected.comment" label="答案点评" placeholder="请填写答案点评" type="textarea" maxlength="100"-->
        <!--                   autosize show-word-limit>-->
        <!--        </van-field>-->
        <van-button v-if="practiseInfo && practiseInfo.type === 4" type="primary" @click="toScore" style="width: 100%;">
          提交
        </van-button>
      </div>
      <r-lack-data v-else></r-lack-data>
    </van-popup>

  </div>
</template>

<script>
import { Dialog } from "vant";
import { transformValue } from "@/utils/index";
import defaultImg from "../../../../assets/boy_img.png";

export default {
  name: "practiseView",
  data () {
    return {
      libraryInfo: this.$store.getters.practiseLibrary,
      selectedIndex: 1,
      practiseTypeOptions: [
        { label: "单选题", value: 1 },
        { label: "多选题", value: 2 },
        { label: "判断题", value: 3 },
        { label: "问答题", value: 4 }
      ],
      practiseInfo: null,
      submitInfo: {
        total: 0,
        subNum: 0,
        persons: []
      },

      answerPersons: [],
      answerPersonsShow: false,
      answerPersonsSelected: null,

      surveyInfo: {
        yes: 0,
        no: 0
      }
    };
  },
  created () {
    this.getPractiseInfo();
    this.getSubmitPerson();
  },
  methods: {
    transform (value) {
      return transformValue("show", value, this.$gatewayHttp.resourse.picture);
    },
    next (value) {
      this.selectedIndex += value;
      this.getPractiseInfo();
    },
    getPractiseInfo () {
      this.practiseInfo = null;
      this.$gatewayHttp.classPractise.getDataInfo({
        cipID: this.libraryInfo.taskID,
        plID: this.libraryInfo.id,
        psSerial: this.selectedIndex
      }).then(res => {
        if (res.Result) {
          this.practiseInfo = {
            id: res.Data["PSID"],
            index: res.Data["PSSerial"],
            title: res.Data["PSContent"],
            answer: res.Data["PSAnswer"],
            analysis: res.Data["PSanalysis"],
            type: res.Data["PSType"],
            optionsNum: res.Data["PSOption"],
            score: res.Data["PSScore"],
            state: res.Data["State"],
            options: res.SODatas ? res.SODatas.map(sub => {
              return {
                id: sub["SOID"],
                name: sub["SOSerial"],
                value: sub["SOContent"]
              }
            }) : [],
            answers: res.Options ? res.Options.map(sub => {
              return {
                id: sub["ID"],
                name: sub["OptionName"],
                total: sub["Total"]
              }
            }) : [],
            isSurvey: res["IsSurvey"],
            falseRate: res["ErrorRate"],
            trueRate: res["CorrectRate"]
          };

          this.getSurveyPerson();
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    getSubmitPerson () {
      this.submitInfo = {
        total: 0,
        subNum: 0,
        persons: []
      };
      this.$gatewayHttp.classPractise.getSubmitPerson({
        cipID: this.libraryInfo.taskID
      }).then(res => {
        if (res.Result) {
          this.submitInfo.subNum = res["SubNum"] || 0;
          this.submitInfo.total = res["Total"] || 0;
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.submitInfo.persons.push({
                studentID: item["StudentID"],
                studentName: item["StudentName"],
                imgSrc: defaultImg
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },

    toAnswer (sub) {
      this.answerPersonsShow = true;
      this.getAnswerInfo(sub);
    },
    getAnswerInfo (sub) {
      this.answerPersonsSelected = null;
      this.$gatewayHttp.classPractise.getAnswerInfo({
        cipID: this.libraryInfo.taskID,
        psID: this.practiseInfo.id,
        studentID: sub.studentID || ''
      }).then(res => {
        if (res.Result) {
          this.answerPersonsSelected = {
            // createTime: formatDate(res.Data["SubmitTime"], "hh:mm:ss"),
            studentID: res.Data["StudentID"] + "",
            studentName: sub.studentName,
            id: res.Data["PAID"] + "",
            answer: res.Data["PAContent"] + "",
            comment: res.Data["PAComment"] + "",
            score: res.Data["PAScore"] || 0,
            master: res.Data["PAMaster"]
          };
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    toScore () {
      this.$gatewayHttp.classPractise.setComment({
        psID: this.practiseInfo.id,
        paID: this.answerPersonsSelected.id,
        score: this.answerPersonsSelected.score,
        saComment: this.answerPersonsSelected.comment
      }).then(res => {
        if (res.Result) {
          this.answerPersonsShow = false;
          this.$toast("答题评分成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("答题评分失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    },
    getSurveyPerson () {
      this.$gatewayHttp.classPractise.getSurveyPerson({
        cipID: this.libraryInfo.taskID,
        psID: this.practiseInfo.id
      }).then(res => {
        if (res.Result) {
          this.surveyInfo = {
            yes: res["YesMaster"],
            no: res["NoMaster"]
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    toSurvey () {
      Dialog.confirm({
        title: '提示',
        message: '是否下发调研？'
      }).then(() => {
        this.$gatewayHttp.classPractise.setSurvey({
          cipID: this.libraryInfo.taskID,
          psID: this.practiseInfo.id
        }).then(res => {
          if (res.Result) {
            this.getPractiseInfo();
            this.$toast("下发调研成功");
          } else if (res.Info) {
            this.$toast(res.Info);
          } else {
            this.$toast("下发调研失败");
          }
        }).catch(() => {
          this.$toast("操作失败");
        });
      }).catch(() => {
      });
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";

.course-practise-view {
  min-height: 100%;
  background-color: #fff;

  .list-col {
    flex: 1;
    min-width: 20vw;
    text-align: center;
    padding: 10px 0;
  }

  .answer-content {
    width: 100%;
    word-break: break-all;

    /deep/ img {
      max-width: 100%;
    }
  }

  .practise-item {
    padding-top: 5px;
    padding-bottom: 16px;

    .practise-title {
      font-size: @font-title * 0.8;
    }

    h4 {
      font-size: @font-title * 0.9;
      padding-left: 10px;
      position: relative;

      &:before {
        content: " ";
        position: absolute;
        top: 50%;
        left: 0;
        width: 5px;
        height: 16px;
        background: #00cbab;
        transform: translateY(-50%);
      }
    }

    .practise-options {
      margin-left: 16px;
    }

    .practise-content {
      margin-left: 16px;
      padding: 3px 10px;
      color: #f56c6c;
      background: #f8f8f8;
    }
  }
}
</style>
